*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.App {
    font-family: sans-serif;
    text-align: center;
    height: 100vh;
    background-color: #161616;
    color: whitesmoke;
  }
  img{
      width : 100px;
  }

  svg{
    fill: whitesmoke;
    margin: 10px;
  }

  .heading{
    font-size: 4rem;
  }
  .location{
    font-size: 1.5rem;
    text-decoration: underline;
  }
  .grids{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 25vh 35vh 20vh 20vh;
    grid-template-areas: "header header"
    "temprature weatherCondi"
    "icons_1 icons_1"
    "icon_2 icon_2";
  }
  .header{
    grid-area: header;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .temprature{
    grid-area: temprature;
    display: flex;
    justify-content: flex-start;
    align-items: baseline;
    font-size: 6rem;
    padding: 60px;
    margin-left: 50px;
  }
  .weatherCondi{
    grid-area: weatherCondi;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .icons_1{
    grid-area: icons_1;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    font-size: 0.75rem;
  }
  .icon_2{
    grid-area: icon_2;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    font-size: 0.75rem;
  }

  button{
    background : transparent;
    border : transparent;
    font-size : 4rem;
    font-weight : 600;
    color : gray
  }

  @media only screen and (max-width:768px){
    div{
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: 0.2fr 0.2fr 0.2fr 0.2fr 0.2fr;
        grid-template-areas: "header" "temprature" "weatherCondi" "icons_1" "icon_2";
    }
}


  .load{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    font-size: 5rem;
  }